.images-carousel {
  .owl-carousel .owl-item {
    position: relative;
  }

  .owl-carousel .image.item {
    position: absolute;
    width: auto;
    height: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    max-width: 100%;
    max-height: 100%;
  }
}