.pages-cards {
  margin: 50px -15px 0;
  padding: 60px 0;
  background: $secondary;

  .card-item {
    text-align: center;

    &--blush span {
      background-image: linear-gradient(transparent calc(75% - 5px), rgba($blush, .5) 5px,
        );
    }

    &--powder span {
      background-image: linear-gradient(transparent calc(75% - 5px), rgba($powder, .5) 5px,
        );
    }

    &--coral span {
      background-image: linear-gradient(transparent calc(75% - 5px), rgba($coral, .5) 5px,
        );
    }
  }

  .card-title {
    margin-bottom: 20px;

    text-align: center;
    font-family: 'Druk Wide Web';
    font-size: 22px;
    line-height: 1.4;
    text-transform: uppercase;

    .t-underline {
      display: inline;


    }

  }


  .card-button {
    display: inline-block;
    margin-top: 20px;
  }

  .owl-pages-cards .owl-dots {
    position: absolute;
    bottom: -50px;
    margin: auto;
    left: 0;
    right: 0;
  }


  .confetti-accent {
    position: absolute;
    top: -10vh;
    right: 0;

    width: 50vw;
    max-width: 400px;

    transform: translateX(20%);
    z-index: 1;

    @include media-breakpoint-up(md) {
      width: 25vw;
      top: -30vh;
    }
  }

  @include media-breakpoint-up(md) {

    margin: 100px -15px 0;
    padding: 30px 0 100px;
  }

}